.list-wrapper {
  width: 1024px;
  margin: 0 auto;
  padding-top: 3rem;
  padding-bottom: 3rem;
  padding-left: 1rem;
  padding-right: 1rem; }

@media (max-width: 1399px) {
  .list-wrapper {
    width: 768px; } }

@media (max-width: 1023px) {
  .list-wrapper {
    width: 512px; } }

@media (max-width: 767px) {
  .list-wrapper {
    width: 100%; } }
