.post-list .post-item {
  padding: 1.5rem;
  -webkit-transition: all .15s ease-in;
  transition: all .15s ease-in; }

.post-list .post-item h2 {
  font-size: 2rem;
  font-weight: 400;
  margin: 0;
  color: #343a40; }

.post-list .post-item h2 a {
  -webkit-transition: all .15s ease-in;
  transition: all .15s ease-in;
  border-bottom: 1px solid transparent; }

.post-list .post-item h2 a:hover {
  color: #228be6;
  border-bottom: 1px solid #228be6; }

.post-list .post-item .date {
  font-size: 0.85rem;
  color: #adb5bd; }

.post-list .post-item p {
  font-weight: 300;
  color: #495057; }

.post-list .post-item .tags {
  font-size: 0.85rem;
  color: #228be6; }

.post-list .post-item .tags a:hover {
  color: #339af0;
  text-decoration: underline; }

.post-list .post-item .tags a + a {
  margin-left: 0.25rem; }

.post-list .post-item:hover {
  background: rgba(34, 139, 230, 0.05); }

.post-list .post-item + .post-item {
  border-top: 1px solid #dee2e6; }
